    trans(color, $duration:1s)
    trans(transform, $duration:1s)
    &[data-disabled="true"]
      pointer-events none
    &.hidden
      visibility hidden
      pointer-events none
    &.-blurry
      trans(opacity, $duration:1s)
      transition-delay 1s
