    &:before, &:after
      width 100%
      height 1px
      background-color currentColor
      display block
      position absolute
      content ''
      trans(background-color, $duration:0.5s)
      pointer-events none
      opacity .6
    &:before
      top 0px
    &:after
      bottom 0px

    &[data-direction="1"]
      transform translateY(-120%)
      transition-duration 0.75s
      
    &[data-direction="1"], &[data-direction="0"]
      .gradient
        opacity 0


    &[data-direction="-1"]
      .gradient
        opacity 1
    
    &[data-thematique="true"]
      transform translateY(-130%)
      addVariantProps(color, false)     
      .gradient
        opacity 0   

    .gradient
      position absolute
      top 'calc(%s * -1)' % ($gutter)
      left 'calc(%s * -1)' % ($gutter)
      width 100vw
      z-index -1
      rp(height, 90px)
      trans(opacity)
      trans(height)
      trans(background, $duration:1.5s)
      pointer-events none
      background 'linear-gradient(0deg, %s 0%, %s 49%, %s 100%)' % (v-bind(gradientColor1) v-bind(gradientColor2) v-bind(gradientColor3))
      [data-isopen="true"]&
        rp(height, 50px)

    .main-nav
      position relative
      width 100%

    .main-nav
      position relative
      width 100%

      .links
        f(row)
        line-height 0 
      
      .link
        [data-thematique="false"]&
          color v-bind(textColor)
</style>
