import { store as scrollStore } from "components/SmoothScroll/store";
import { WebGLSize, WebGLSyncData } from "components/WebGL/gl";
import { map } from "lodash";
import { store as interfaceStore } from 'store/interface';
import { Box3, Camera, Object3D, PerspectiveCamera, Vector3 } from 'three';
import { store as loaderStore } from "ui/Loader/store";
import { mainWebGLRenderer } from "./three";

/**
 * Return the unit size of the viewport (threejs unit)
 * @param distance Distance between the object3D position and the camera position
 * @param camera Camera of the Scene
 * @returns WebGLSize
 */
const calculateUnitSize = (distance:number, camera:PerspectiveCamera) : WebGLSize => {
  const vFov = camera.fov * (Math.PI / 180);
  const height = 2 * Math.tan(vFov / 2) * distance;
  const width = height * camera.aspect;

  return { width, height };
}

/**
 * Sync the object3D and an HTMLElement. Will write a WebGLSyncData object into the userData of the object3D
 * Available here: object3D.userData.sync<WebGLSyncData>
 * @param obj3d Object3D instance
 * @param el HTMLelement
 * @param camera Camera of the scene
 * @param resetSync If we need to reset the userData sync object. Needs this if we resize the viewport (bounds changed after that)
 */
const syncMeshToDOM = (obj3d : Object3D, el : HTMLElement, camera : PerspectiveCamera, resetSync = false) : void => {
  
  const unit = calculateUnitSize(camera.position.z - obj3d.position.z, camera);
  
  if(!obj3d.userData.sync || resetSync) {
    const top = el.offsetTop;
    const left = el.offsetLeft;
    const width = el.offsetWidth;
    const height = el.offsetHeight;

    obj3d.userData.sync = {
      scale: new Vector3(1,1,1),
      position: new Vector3(0,0,0),
      ref_bounds: { left, top}, 
      bounds: {width, height, left, top}, 
      progress: NaN,
    } as WebGLSyncData;
  }
  else{
    obj3d.userData.sync.bounds.top = obj3d.userData.sync.ref_bounds.top - scrollStore.getScroll();
    
    //basic progress
    const topProgress = (obj3d.userData.sync.bounds.top) / interfaceStore.viewport.webglHeight;
    const offsetProgress = (obj3d.userData.sync.bounds.height) / interfaceStore.viewport.webglHeight;
    obj3d.userData.sync.progress = topProgress + (offsetProgress/2);
  }
 
  const sx = obj3d.userData.sync.bounds.width / interfaceStore.viewport.width;
  const sy = obj3d.userData.sync.bounds.height / interfaceStore.viewport.webglHeight;
  obj3d.userData.sync.scale.set(unit.width * sx, unit.height * sy, obj3d.scale.z);

  let x = -(unit.width / 2) + (obj3d.userData.sync.scale.x / 2);
  x += (obj3d.userData.sync.bounds.left / interfaceStore.viewport.width) * unit.width;

  let y = (unit.height / 2) - (obj3d.userData.sync.scale.y / 2);
  y -= (obj3d.userData.sync.bounds.top / interfaceStore.viewport.webglHeight) * unit.height;

  obj3d.userData.sync.position.set(x,y,obj3d.position.z);
}

/**
 * Get the progression of the object3D inside the whole smooth scroll section.
 * @param obj3d Object3D instance
 * @returns Number Progress ratio
 */
const getScrollProgress = () : number =>  {
  const scrollWrapperEL = document.documentElement as HTMLElement;
  return scrollStore.getScroll() / (scrollWrapperEL.offsetHeight - interfaceStore.viewport.webglHeight);
}

/**
 * Get the 3d relative point to 2d coordinates
 * @param p 2d coordinates
 * @param viewport Screen Resolution
 * @return [x,y] axis normalized coordinates 
 */
const transform2DPointTo3D = (p : {x:number,y:number}, viewport:WebGLSize) : {x:number,y:number} =>  {
  return {
    x: (p.x / viewport.width) * 2 - 1,
    y: -(p.y / viewport.height) * 2 + 1
  }
}


//---- Spritesheet
/**
 * Preload all image from an image sequence
 * @param totalFrames Number instance
 * @param path WebGLSpritesheetPath object 
 */
const preloadImageSequence = (
  config:{total:number, startAt:number, requested:number},
  path : WebGLSpritesheetPath
) => {

  const isHighResVersion = interfaceStore.highResVersion;
  let folderImage = isHighResVersion ? "ktx2/" : "png/";
  
  let loader = loaderStore.getLoader("spritesheet");
  
  if(!loader) {
    loader = loaderStore.getLoader("texture");
    folderImage = "png/";
  }
  //custom filename
  path.url += folderImage;

  if(folderImage === "ktx2/") {
    path = {...path, files_extension: path.files_extension + ".ktx2"};
  }

  const imagesUrls = map(new Array(config.requested), (val, index) => {
    const filename = getImageFilename(config.total, config.startAt + index, path);
    return `${path.url}${filename}`;
  });

  return {loader, urls:imagesUrls};
}

