/**
 * Get the right filename for an image sequence (000.png if > 100 frames), (00.png if > 10 and < 100 frames)
 * @param totalFrames Total frame number
 * @param frameNumber The frame number
 * @param path WebGLSpritesheetPath object 
 * @returns String
 */
const getImageFilename = (totalFrames:number, frameNumber : number, path : WebGLSpritesheetPath) : string => {
  const prefixNames = totalFrames > 100 ? '00' : '0';
  const pathUserPrefix = path.prefix_filename ?? '';

  let filename = `${pathUserPrefix}${frameNumber}.${path.files_extension}`;
    
  if(frameNumber < 10) {
    filename = `${pathUserPrefix}${prefixNames}${frameNumber}.${path.files_extension}`;
  }

  if(frameNumber >= 10 && frameNumber < 100) {
    filename = `${pathUserPrefix}${prefixNames.substring(1)}${frameNumber}.${path.files_extension}`;
  }

  return filename;
}

const getPixelSize = (object : Object3D, camera:Camera) => {
  // Get the bounding box of the hotspot in world coordinates
  const boundingBox = new Box3().setFromObject(object);
  // Get the min and max world coordinates
  const minWorld = boundingBox.min.clone();
  const maxWorld = boundingBox.max.clone();

  // Convert world coordinates to screen coordinates
  minWorld.project(camera);
  maxWorld.project(camera);

  // Calculate the screen width and height
  return {
    width: (Math.abs(maxWorld.x - minWorld.x) * mainWebGLRenderer!.domElement.offsetWidth) / 2,
    height: (Math.abs(maxWorld.y - minWorld.y) * mainWebGLRenderer!.domElement.offsetHeight) / 2
  }
}



